import {

    Stack,
    TextField,
    Toolbar,
    Typography
} from '@mui/material';
import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import AddIcon from '@mui/icons-material/Add';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableFooter from '@mui/material/TableFooter';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import TableHead from '@mui/material/TableHead';
import DeleteIcon from '@mui/icons-material/Delete';
import ManageSearchIcon from '@mui/icons-material/ManageSearch';
import EditIcon from '@mui/icons-material/ModeEditOutlineOutlined';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import FirstPageIcon from '@mui/icons-material/FirstPage';
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight';
import LastPageIcon from '@mui/icons-material/LastPage';
import Button from '@mui/material/Button'
import axios from 'axios';
import { variables } from "../../variables";
import Popup from '../../components/Popup';
import CustomizedDialogs from '../../components/Dialog/CustomizedDialogs'

// import Edit from './Edit';

 import Notification from '../../components/Notifications';

import NewUser from './Newuser';



interface TablePaginationActionsProps {
    count: number;
    page: number;
    rowsPerPage: number;
    onPageChange: (
        event: React.MouseEvent<HTMLButtonElement>,
        newPage: number,
    ) => void;
}

function TablePaginationActions(props: TablePaginationActionsProps) {
    const theme = useTheme();
    const { count, page, rowsPerPage, onPageChange } = props;

    const handleFirstPageButtonClick = (
        event: React.MouseEvent<HTMLButtonElement>,
    ) => {
        onPageChange(event, 0);
    };

    const handleBackButtonClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        onPageChange(event, page - 1);
    };

    const handleNextButtonClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        onPageChange(event, page + 1);
    };

    const handleLastPageButtonClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        onPageChange(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
    };

    return (
        <Box sx={{ flexShrink: 0, ml: 2.5 }}>
            <IconButton
                onClick={handleFirstPageButtonClick}
                disabled={page === 0}
                aria-label="first page"
            >
                {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
            </IconButton>
            <IconButton
                onClick={handleBackButtonClick}
                disabled={page === 0}
                aria-label="previous page"
            >
                {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            </IconButton>
            <IconButton
                onClick={handleNextButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="next page"
            >
                {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
            </IconButton>
            <IconButton
                onClick={handleLastPageButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="last page"
            >
                {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
            </IconButton>
        </Box>
    );
}

function createData(name: string, calories: number, fat: number) {
    return { name, calories, fat };
}


export default function Users() {
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(5);
    const [rows, setRows] = useState([]);
    const [recordForEdit, SetRecordForEdit] = useState(null)
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [openPopup, setOpenPopup] = useState(false)
    const [openPopup2, setOpenPopup2] = useState(false)
    const [searchApiData, setSearchApiData] = useState('');
    const [filterval, setFilterval] = useState('');
    const [openFirst, setOpenFirst] = React.useState(false);

  


    // Paginations
    const emptyRows =
        page > 0 ? Math.max(0, (1 + page) * rowsPerPage - rows.length) : 0;

    const handleChangePage = (
        event: React.MouseEvent<HTMLButtonElement> | null,
        newPage: number,
    ) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (
        event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>,
    ) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    // Open in Popup Modal

    const openInPopup = row => {
        SetRecordForEdit(row)
        setOpenPopup(true)
    }

    // Updating Database
    const updatedata = (values) => {
        console.log(values)
        const Endpoint = `Customer`
        axios.put(variables.API_URL + Endpoint, {
            id: values.id,
            name: values.Name,
            account: values.account,
            employmenttype: values.employmenttype,
            employer: values.EmployerName,
            position: values.Position,
            homeaddress: values.FullAddress,
            mobile: values.Mobile,
            netmonthsalary: values.Netmonthsalary,
            datejoining: values.Datejoining,
            preemployer: values.Previousemployer,
            otherincome: values.otherincome,
            salarycrediteab: values.salarycrediteab,
            citizenship: values.citizenship,
            otherbanks: values.otherbanks,
            eabopenedaccount: values.EABdateopened,
            liabilities: values.liabilitiesotherbanks,
            gender: values.gender
        })
            .then((response) => {
                setNotify({
                    isOpen: true,
                    message: 'Updated Successfully',
                    type: 'success'
                }
                )
                setOpenPopup(false);
            })
            .catch((e) => {
                console.log("Hello")
            })
    }

    // Deleting Data

    const DeleteRow = (row) => {

        const Endpoint = `Employee/${row.id}`

        axios.delete(variables.API_URL + Endpoint)
            .then(res => {
                setNotify({
                    isOpen: true,
                    message: 'Sucessfully Deleted',
                    type: 'error'
                }
                )

               
            })
            .catch(error => {

                console.log("Error")

            })
    }

    // Insert Data

    const insernewdata = (data) => {

        const Endpoint = 'Employee'
        axios.post(variables.API_URL + Endpoint, {
            name: data.Name,
            email: data.email,
            phone: data.Phone,
            password: data.password,
            title: data.title,
            role: data.role

        })
            .then((response) => {

                setNotify({
                    isOpen: true,
                    message: 'User Created, Thanks',
                    type: 'success'
                }

                )

                setOpenPopup2(false)

                const Newrec = response.data;

                setRows(oldrows => [...oldrows, Newrec])

            })
            .catch((e) => {
                console.log(data)
                setNotify({
                    isOpen: true,
                    message: 'Something goes wrong',
                    type: 'error'
                }
                )
            })
    }








    const getEmployee = async () => {
        try {
            const Endpoint = `Employee`
            const data = await axios.get(variables.API_URL + Endpoint);

            setRows(data.data);


        }
        catch (e) {
            console.log(e);
        }
    }

    // Displaying Data

    useEffect(() => {
        getEmployee();
    }, []);

    const handleFilter = (e) => {
        if (e.target.value === '') {
            getEmployee();
        }
        else {
            const filterResult = rows.filter(item => item.name.toLowerCase().includes(e.target.value.toLowerCase()));
            setRows(filterResult)
        }
        setFilterval(e.target.value)
    }

    return (
        <>
            <Helmet>
                <title> HR - Hires</title>
            </Helmet>
            <TableContainer component={Paper}>

                <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
                    <Button variant="contained" startIcon={<AddIcon />} onClick={() => setOpenPopup2(true)}>
                        Add
                    </Button>


                    <Box sx={{ display: 'flex', alignItems: 'flex-end' }}>

                        <TextField id="standard-basic" label="Search Employee" variant="standard" placeholder='Search' value={filterval} onInput={(e) => handleFilter(e)} />

                    </Box>
                </Stack>

                <Table sx={{ minWidth: 500 }} aria-label="custom pagination table">
                    <TableHead>
                        <TableRow >
                            <TableCell>ID</TableCell>
                            <TableCell >Name</TableCell>
                            <TableCell>Phone</TableCell>
                            <TableCell >Title</TableCell>
                            <TableCell >E-mail</TableCell>
                            <TableCell>Role</TableCell>
                           
                            <TableCell>Edit</TableCell>
                            <TableCell>Delete</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {(rowsPerPage > 0
                            ? rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                            : rows
                        ).map((row) => (
                            <TableRow key={row.id}>
                                <TableCell component="th" scope="row">
                                    {row.id}
                                </TableCell>
                                <TableCell>
                                    {row.name}
                                </TableCell>
                                <TableCell >
                                    {row.phone}
                                </TableCell>
                                <TableCell >
                                    {row.title}
                                </TableCell>
                                <TableCell >
                                    {row.email}
                                </TableCell>
                                <TableCell >
                                    {row.role}
                                </TableCell>
                              
                                
                                <TableCell >
                                    <Button variant="outlined" onClick={() => DeleteRow(row)} startIcon={<DeleteIcon />} color="error" />
                                </TableCell>

                            </TableRow>
                        ))}
                        {emptyRows > 0 && (
                            <TableRow style={{ height: 53 * emptyRows }}>
                                <TableCell colSpan={6} />
                            </TableRow>
                        )}
                    </TableBody>
                    <TableFooter>
                        <TableRow>
                            <TablePagination
                                rowsPerPageOptions={[5, 10, 25, { label: 'All', value: -1 }]}
                                colSpan={3}
                                count={rows.length}
                                rowsPerPage={rowsPerPage}
                                page={page}
                                SelectProps={{
                                    inputProps: {
                                        'aria-label': 'rows per page',
                                    },
                                    native: true,
                                }}
                                onPageChange={handleChangePage}
                                onRowsPerPageChange={handleChangeRowsPerPage}
                                ActionsComponent={TablePaginationActions}
                            />
                        </TableRow>
                    </TableFooter>
                </Table>
            </TableContainer>

            <Popup
                openPopup={openPopup}
                setOpenPopup={setOpenPopup}
                title="Edit Hire"
            >
                <Typography>Welcome</Typography>
            </Popup>

            <Popup
                openPopup={openPopup2}
                setOpenPopup={setOpenPopup2}
                title="New User"
            >
               <NewUser insernewdata={insernewdata} setOpenPopup2={setOpenPopup2} />
            </Popup>

            <Notification
                notify={notify}
                setNotify={setNotify}
            />


            <>



            </>
        </>
    );
}