import React from 'react';

import TextField from '@mui/material/TextField';

import {
    Button,
    Radio,
    RadioGroup,
    FormControlLabel,
    FormControl,
    InputLabel,
    Select,
    FormLabel,
    FormHelperText,
    FormGroup,
    Checkbox,
    Container,
    Box,
    Stack,
    MenuItem,
} from '@mui/material';

// ----------------------------------------------------------------------

import { useForm } from "react-hook-form";

export default function NewUser(props) {
    const { insernewdata, setOpenPopup2 } = props

    const handleCancel = () => {
        return setOpenPopup2(false)
    }
    const { register, handleSubmit, watch, formState: { errors } } = useForm();

    const onSubmit = (data) => insernewdata(data);



    return (
        <>


            <Container maxWidth="lg" sx={{ margin: 0, }}>
                <Box
                    sx={{ padding: '10px', borderRadius: '10px' }}
                >

                    <form onSubmit={handleSubmit(onSubmit)}>
                        <Stack direction="row" spacing={2}>
                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    name="fullName"
                                    label="Full Name"
                                    variant="outlined"
                                    fullWidth
                                    {...register("Name", { required: "FullName is required." })}
                                    error={Boolean(errors.Name)}
                                    helperText={errors.Name?.message}
                                />
                            </Box>

                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="E-mail"
                                    variant="outlined"
                                    fullWidth
                                    name="email"
                                    {...register("email", { required: "Emial is required." })}
                                    error={Boolean(errors.email)}
                                    helperText={errors.email?.message}
                                />
                            </Box>
                           
                        </Stack>
                        <br/>
                        <Stack direction="row" spacing={2}>
                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    name="Phone"
                                    label="Phone Number"
                                    variant="outlined"
                                    fullWidth
                                    {...register("Phone", { required: "Phone is required." })}
                                    error={Boolean(errors.Name)}
                                    helperText={errors.Name?.message}
                                />
                            </Box>

                            <Box flex={4}>
                                <TextField
                                    id="outlined-basic"
                                    label="Password"
                                    variant="outlined"
                                    fullWidth
                                    name="password"
                                    {...register("password", { required: "Password is required." })}
                                    error={Boolean(errors.email)}
                                    helperText={errors.email?.message}
                                />
                            </Box>
                           
                        </Stack>
                        <br />
                        <Stack direction="row" spacing={2}>
                            <Box flex={4}>
                                <FormControl sx={{ width: '110px' }}>
                                    <InputLabel id="possitionId-label">Title</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-label"
                                        id="countryid"
                                        label="title"
                                        sx={{ width: '225px' }}

                                        {...register("title", { required: "Title is required." })}
                                        error={Boolean(errors.title)}
                                        helperText={errors.title?.message}
                                    >
                                        <MenuItem value={'Relation Manager'}>Relation Manager</MenuItem>
                                        <MenuItem value={'Credit Analysist'}>Credit Analysist</MenuItem>
                                        <MenuItem value={'Credit Administartion'}>Credit Administartion</MenuItem>
                                        <MenuItem value={'Sharia Office'}>Sharia Office</MenuItem>
                                        <MenuItem value={'Operation'}>Operation</MenuItem>
                                    

                                    </Select>

                                </FormControl>
                            </Box>
                            <Box flex={4}>
                                <FormControl sx={{ width: '110px' }}>
                                    <InputLabel id="possitionId-label">Role</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-label"
                                        id="countryid"
                                        label="role"
                                        sx={{ width: '225px' }}

                                        {...register("role", { required: "Role is required." })}
                                        error={Boolean(errors.role)}
                                        helperText={errors.role?.message}
                                    >
                                        <MenuItem value={'RM'}>Relation Manager</MenuItem>
                                        <MenuItem value={'CA'}>Credit Analysist</MenuItem>
                                        <MenuItem value={'CAD'}>Credit Administartion</MenuItem>
                                        <MenuItem value={'OP'}>Operation</MenuItem>


                                    </Select>

                                </FormControl>
                            </Box>
                            


                        </Stack>
                        <br />
                      
                        <Stack direction="row" spacing={2}>

                            <Button variant="contained" color="primary" type="submit" className="btns">
                                Create
                            </Button>
                            <Button variant="contained" color="error" onClick={handleCancel} className="btns">
                                Cancel
                            </Button>
                        </Stack>


                    </form>
                </Box>
            </Container>
        </>
    );
}
